@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial: 16px;
  --prime-color:#e81230;
  --startPage-background-color:#FFF6F3; }

* {
  margin: 0;
  padding: 0; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 16px)/1.35 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

body {
  background-image: url("/images/FON.svg");
  background-position: center;
  background-repeat: no-repeat; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em; }

h5, h6 {
  font-weight: bold; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 2em; }

li {
  list-style-position: inside; }

a {
  text-decoration: none; }

.btn {
  display: inline-block;
  border: 3px solid var(--prime-color, #e81230);
  border-radius: 5px;
  background: none;
  padding: 0.7em 2em;
  color: var(--prime-color, #e81230);
  text-decoration: none;
  font-size: 1.2em;
  text-align: center; }

.pennant {
  position: relative;
  display: inline-block;
  padding: 0.175em;
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 50%;
  cursor: default;
  text-align: center;
  vertical-align: baseline; }

.icon {
  position: relative;
  display: inline-block;
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 50%;
  color: var(--prime-color, #e81230);
  font-size: 1.2em;
  line-height: 2em;
  text-decoration: none;
  width: 2em;
  height: 2em;
  text-align: center; }

.icon:before {
  display: inline-block; }

.startPage-btn {
  padding: 0.5em 1em;
  color: var(--prime-color, #e81230);
  border: 1.5px solid var(--prime-color, #e81230);
  border-radius: 25px; }

.icon-nav {
  font-size: 1.2em;
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 50%;
  color: var(--prime-color, #e81230);
  padding: 0.5em;
  text-align: center; }

body {
  padding: 2rem; }

.media-wrapper {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap; }

.media {
  width: 100%;
  background: black;
  border-radius: 0.5rem;
  margin: 0 2rem 2rem 0;
  position: relative; }
  .media.ratio-1x1:before {
    display: block;
    content: '';
    padding-top: 100%; }
  .media.ratio-1x1:after {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    content: "1 x 1";
    color: white;
    font-size: 2rem;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }
  .media.ratio-16x9:before {
    display: block;
    content: '';
    padding-top: 56.25%; }
  .media.ratio-16x9:after {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    content: "16 x 9";
    color: white;
    font-size: 2rem;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }
  .media.ratio-16x10:before {
    display: block;
    content: '';
    padding-top: 62.5%; }
  .media.ratio-16x10:after {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    content: "16 x 10";
    color: white;
    font-size: 2rem;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }
  .media.ratio-9x16:before {
    display: block;
    content: '';
    padding-top: 177.77778%; }
  .media.ratio-9x16:after {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    content: "9 x 16";
    color: white;
    font-size: 2rem;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }
  .media.ratio-3x2:before {
    display: block;
    content: '';
    padding-top: 66.66667%; }
  .media.ratio-3x2:after {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    content: "3 x 2";
    color: white;
    font-size: 2rem;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }
  .media.ratio-2x3:before {
    display: block;
    content: '';
    padding-top: 150%; }
  .media.ratio-2x3:after {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    content: "2 x 3";
    color: white;
    font-size: 2rem;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }

.tabs {
  height: 40em;
  width: 60em;
  clear: both;
  position: relative;
  text-align: center;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  background: white; }

.tab {
  float: left; }

.tab p {
  margin: 1em 0;
  padding: 1em;
  width: 100%; }

.tab label {
  position: relative;
  background: white;
  color: var(--prime-color, #e81230);
  padding: 1em;
  font-size: 2em;
  display: flex; }

.tab [type=radio] {
  display: none; }

#aboutUs {
  background: white; }

#home_text {
  background: var(--prime-color, #e81230);
  padding: 2em;
  margin: 5em auto; }

.startPage-img {
  width: 50%; }

.content {
  position: absolute;
  top: 8em;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  margin: 0 auto;
  padding: 2em; }

[type=radio]:checked ~ label {
  z-index: 2; }

[type=radio]:checked ~ label ~ .content {
  z-index: 1; }

body {
  padding: 1em;
  margin: 1em; }

.header-top {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  border-bottom: 3px solid var(--prime-color, #e81230);
  padding: 1em; }

header ul {
  list-style: none inside;
  padding: 0; }

header ul li {
  display: inline-block; }

.home-text {
  margin: 0 auto; }

.home-text img {
  width: 100%;
  background: var(--prime-color, #e81230);
  padding: 1em; }

.footer-main {
  display: flex;
  width: 100%;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  border-top: 2px solid var(--prime-color, #e81230); }

.footer-section-information {
  width: 25%;
  padding: 0.1em;
  margin: 0.1em; }
  .footer-section-information ul {
    list-style: none inside; }
  .footer-section-information a {
    color: black;
    text-decoration: none; }
    .footer-section-information a:hover {
      color: var(--prime-color, #e81230); }

.give-blood-project {
  width: 100%; }

.footer-logo {
  display: flex;
  align-items: center;
  justify-content: center; }
